.rightbar_parent {
    margin-top: 1rem;
    position: fixed;
    right: 0;
    top: 0;
    bottom: 0;
    width: 66%;
    overflow-y: auto;
    display: flex;
    justify-content: center;
    align-items: center;
}
.rightbar {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    background-color: var(--bg-color);
    width: 100%;
}

.rightbar div {
    border: 1px solid var(--fg-color);
    width: 20%;
    padding: 1rem;
    margin: 1rem;
    border-radius: 1rem;
    transition: background-color 0.3s ease;
    background-color: var(--bg-color);
}

.rightbar div h1 {
    font-size: 7vw;
    font-weight: bolder;
    color: var(--fg-color);
    transition: transform 0.6s ease, color 0.6s ease;
    margin: 0;
}

.rightbar div:hover {
    background-color: var(--fg-color);
}

.rightbar div:hover h1 {
    color: var(--bg-color);
    transform: rotateZ(360deg);
}

@media (max-width:650px) {
    .rightbar_parent {
        position: static;
        width: 100%;
        margin-bottom: 1rem;
    }
    .rightbar {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .rightbar div {
        width: 10%;
        margin: 1%;
    }
}